<template>
    <div id="login">
        <ul>
            <li>
                <input type="text" ref="name" onkeyup="value=value.replace(/[^\w\.\/]/ig,'')">
                <img class="img" src="http://cdn.xqit.net/ht/img/login3.png" />
            </li>
            <li>
                <input type="password" ref="word" onkeyup="value=value.replace(/[^\w\.\/]/ig,'')">
                <img class="img" src="http://cdn.xqit.net/ht/img/password3.png" />
            </li>
            <li>
                <button @click="jump">登陆</button>
            </li>
        </ul>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'login',
    data(){
        return{
            
        }
    },
    methods:{
        jump(){
            var that = this;
            var name = this.$refs.name.value;
            var word = this.$refs.word.value;
            let formData = new FormData();
            formData.append('username',name);
            formData.append('password',word);
            if(name == "" || name == null){
                this.$message.error('账号不能为空')
            }else if(word == "" || word == null){
                this.$message.error('密码不能为空')
            }else{
                axios.post(this.baseUrl+'/ydjxzg/htlog/htlogin.action', formData)
                .then(function (res) {
                    console.log(res);
                    if(res.data.mess == true){
                        that.$message.success('登陆成功')
                        that.$router.push({path: '/index'})
                    }else{
                        that.$message.error('账号或密码输入有误')
                    }
                })
            }
        }
    }
}
</script>

<style>
#login{
    position: fixed;
    width: 100%;
    height: 100%;
    background-image: url("http://cdn.xqit.net/ht/img/login.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}
#login ul{
    position: absolute;
    right: 0;
    top: 0;
    margin: 18% 17.2% 0 0;
}
#login ul li{
    margin-top: 35px;
    width: 320px;
}
#login ul li input{
    width: 89%;
    height: 30px;
    background-color:transparent;
    border: none;
    border-bottom: 3px #ffffff solid;
    color:#ffffff;
    caret-color:#ffffff;
    padding-left: 36px;
    outline:none;
    font-size: 18px;
}
#login ul li button{
    width: 100%;
    height: 36px;
    margin-top: 26px;
}
.img{
    float: left;
    margin-top: -36px;
    width: 25px;
    height: 25px;
}
</style>



